import { ENVChainId, WETH, ETHs } from 'sdk'

const getLiquidityUrlPathParts = ({ quoteTokenAddress, tokenAddress }) => {
  const chainId = ENVChainId

  const wethAddressString = WETH[chainId]?.address

  const ethSymbol = ETHs[chainId]?.symbol

  const quoteTokenAddressString: string = quoteTokenAddress ? quoteTokenAddress[chainId] : null

  const tokenAddressString: string = tokenAddress ? tokenAddress[chainId] : null

  const firstPart =
    !quoteTokenAddressString || quoteTokenAddressString === wethAddressString ? ethSymbol : quoteTokenAddressString
 
  const secondPart = !tokenAddressString || tokenAddressString === wethAddressString ? ethSymbol : tokenAddressString
 
  return `${firstPart}/${secondPart}`
}

export default getLiquidityUrlPathParts
